<template>
  <div id="pictrueBig">
    <ul>
      <li v-if="xyfgda.length">
        <img :src="xyfgda[index].FileUrl" />
        <p>{{ xyfgda[index].Title }}</p>
      </li>
    </ul>
    <p class="pages">
      
      {{ (index+1) }} &nbsp;&nbsp; of&nbsp;&nbsp; {{ xyfgda.length }}
    </p>
  </div>
</template>
<script>
export default {
  props: {
    xyfgda: Array,
    index: Number,
  },
  data() {
    return {};
  },
  beforeCreate() {},
  mounted() {
    
  },
  methods: {},
};
</script>
<style  scoped>
#pictrueBig {
  width: 1021px;
  height: 671px;
  display: flex;
  position: relative;
}
#pictrueBig ul {
  width: 1021px;
  background: #fff;
  overflow: hidden;
}
#pictrueBig ul li {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 20px;
  margin-bottom: 10px;
}
#pictrueBig ul li img {
  object-fit: cover;
  width: 100%;
  height: 580px;
}
#pictrueBig ul li p {
  text-align: center;
  line-height: 24px;
}
.pages {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 20px;
}
</style>





