<template>
  <div class="xyfg">
    <div class="xyfcb">
      <div>
        <ul class="xyfgcon">
          <!-- <li>
          <img src="../assets/img/xyfg_img5.jpg" alt />
          <p>校园风光</p>
        </li>
        <li>
          <img src="../assets/img/xyfg_img5.jpg" alt />
          <p>校园风光</p>
        </li>
        <li>
          <img src="../assets/img/xyfg_img5.jpg" alt />
          <p>校园风光</p>
        </li>
        <li>
          <img src="../assets/img/xyfg_img5.jpg" alt />
          <p>校园风光</p>
        </li>
        <li>
          <router-link to="hh">
            <img src="../assets/img/xyfg_img5.jpg" alt />
            <p>校园风光</p>
          </router-link>
        </li>
        <li>
          <img src="../assets/img/xyfg_img5.jpg" alt />
          <p>校园风光</p>
        </li>
        <li>
          <img src="../assets/img/xyfg_img5.jpg" alt />
          <p>校园风光</p>
        </li>
        <li>
          <img src="../assets/img/xyfg_img5.jpg" alt />
          <p>校园风光</p>
        </li> -->
          <li
            v-for="(item, index) in xyfgda"
            :key="index"
            @click="getIndex(index)"
          >
            <!-- <router-link :to="{ path: '/list/content', query: { id: item.id } }"> -->
            <img :src="item.FileUrl" />
            <p>{{ item.Title }}</p>
            <!-- </router-link> -->
          </li>
        </ul>
        <pageMove @pageindex="pageF" :pageOption="pageOption"></pageMove>
      </div>

      <div class="rmzyB">
        <rmzy></rmzy>
      </div>
    </div>
    <transition>
      <div class="pictrueBox" ref="Pictrue">
        <div class="showB">
          <i @click="close" class="coloes"></i>
          <i class="Left_btn" @click="change_Index(1)"></i>
          <pictrue_Big
            :xyfgda="xyfgda"
            :index="currentPage"
            ref="Pictrue_event"
          ></pictrue_Big>
          <i class="Right_btn" @click="change_Index(0)"></i>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import rmzy from "./rmzy";
import pictrue_Big from "@/components/componentlist/pictrueBig";
export default {
  components: { rmzy, pictrue_Big },
  name: "xyfg",
  data() {
    return {
      xyfgda: [],
      routerList: {},
      pageOption: {
        pageIndex: 1,
        pageTotal: 0,
        pageSize: 8,
      },
      pageRouterList: 0,
      currentPage: 0,
    };
  },
  created() {
    var _this = this;
    _this.pageRouterList = _this.$route.query.cid;
    _this.getDATA();
  },
  watch: {},
  methods: {
    getDATA() {
      var _this = this;

      var temp = _this.Base.GetFilesInfoByCid(
        "",
        _this.pageRouterList,
        "",
        "",
        _this.pageOption.pageIndex,
        "",
        _this.pageOption.pageSize
      );
      _this.xyfgda = temp.GetData;

      _this.pageOption.pageTotal = temp.Total;
    },
    pageF: function (data) {
      this.pageOption.pageIndex = data;
      this.getDATA();
    },
    getIndex: function (e) {
      var that = this;
      that.$set(this, "currentPage", e);
      
      that.$refs.Pictrue.style.display = "flex";
    },
    close: function (e) {
      var that = this;
      that.$refs.Pictrue.style.display = "none";
      // console.log(that.$refs.Pictrue_event.change_Index()); //ref调用子组件的方式
    },
    change_Index: function (e) {
      // if (this.currentPage > 8) return;
      e < 1
        ? this.$set(
            this,
            "currentPage",
            (this.currentPage+1) >= this.xyfgda.length
              ? 0
              : (this.currentPage+1)
          )
        : this.$set(
            this,
            "currentPage",
            (this.currentPage - 1) < 0 ? this.xyfgda.length : (this.currentPage - 1)
          );
      
    },
  },
  mounted() {
    var _that = this;

    _that.getDATA();
  },
};
</script>
<style  scoped>
.xyfg {
  margin-bottom: 41px;
}
.xyfcb {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.xyfcb .xyfgcon {
  width: 900px;

  /* display: flex;
  justify-content: space-between;
  flex-wrap: wrap; */
  overflow: hidden;
}
.xyfcb .xyfgcon li {
  width: 435px;
  height: 353px;
  float: left;
}
.xyfcb .xyfgcon li:nth-child(2n) {
  margin: 0 0 40px 20px;
}
.xyfcb .xyfgcon img {
  width: 100%;
  height: 290px;
  transition: all 0.4s;
}
.xyfcb .xyfgcon p {
  width: 100%;
  height: 60px;
  font-family: MicrosoftYaHei;
  font-size: 24px;
  line-height: 60px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
  color: #333333;
  text-align: center;
  background-color: #ffffff;
  box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, 0.05);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.xyfcb .rmzyB {
  width: 260px;
}
.xyfcb .xyfgcon li:hover img {
  width: 101%;
  height: 295px;
}
.pictrueBox {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9;
  display: none;
  background-color: rgba(0, 0, 0, 0.6);
  transition: all 0.4s;
}
.pictrueBox .coloes {
  position: absolute;
  display: block;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  /* border: 2px solid red; */
  right: -30px;
  top: -30px;
  z-index: 2;
  background-image: url(../assets/img/colse_icon.png);
  background-size: cover;
  cursor: pointer;
}
.showB {
  margin: auto;
  position: relative;
}
.Left_btn,
.Right_btn {
  width: 48px;
  height: 48px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  z-index: 39;
}
.Left_btn {
  left: -100px;
  background-image: url(../assets/img/left_btn.png) !important;
}
.Right_btn {
  right: -100px;
  background-image: url(../assets/img/right_btn.png) !important;
}
</style>



